@import 'vars.scss';
@import 'helpers.scss';
@import 'base.scss';
@import 'calendar.scss';
@import 'todo.scss';

html {
    font-size: 18px;
    font-weight: 400;
    font-family: "Hind", "Segoe UI", Helvetica, sans-serif;
    color: #333;
}

body {
    background: linear-gradient(to bottom, #334, #446);
    background-size: cover;
    background-attachment: fixed;
    height: 100vh;
    margin: 0;
}

[v-cloak] {
    display: none !important;
}

h1 {
    font-size: 1.5rem;
    line-height: 3rem;
    font-weight: 300;
    margin: 0;
    display: inline;
}

.wrap {
    margin: 0 auto;
    max-width: 800px;
    text-align: center; 
}

.app {
    box-shadow: 0 .8rem 2rem rgba(0,0,0,0.5);
    border-radius: 4px;
    overflow: hidden;
    margin: 1.2rem 2rem 2.8rem;
    @media screen and (max-width: 768px) {
        margin: 0;
    }
}

.toolbar {
    background: #eee;
    height: 3rem;
}

.container {
    display: flex;
    background: #fff;
    overflow: hidden;
}





td.selected {
    box-shadow: inset 0 0 0 2px #28f;
}