.todo {
    background: white;
    border: none;
    padding: 1rem;
    width: 21rem;
    margin: 0;
    position: relative;
    flex: 1;

    &:before {
        content: '';
        display: block;
        position: absolute;
        left: -10px;
        width: 10px;
        bottom: 0;
        top: 0;
        background: linear-gradient(to left, rgba(#000,0.1), transparent);
    }

    input[type=text] {
        outline: none;
        padding: .5rem;
        font-size: 18px;
        color: #333;
        border: 2px solid $line-colour;
        width: 100%;
        margin-bottom: 1rem;
    }

    ul {
        text-align: left;
        padding: 0;
        margin: 0;
        list-style: none;
    }
    
    li {
        height: 2rem;
        line-height: 2rem;
        border-bottom: 1px solid $line-colour;
    }
}

.todo-date {
    font-size: 1.2rem;
    font-weight: 300;
    color: #555;
    border-bottom: 1px solid $line-colour;
    margin-bottom: 1rem;
}

.full-todo {
    position: absolute;
    background: #fff;
}