.calendar {
    background: $line-colour;
    min-height: 24rem;
}

.calendar-full {
    width: 100%;
    height: 80vh;
    table {
        width: 100% !important;
    }
    thead td {
        height: 2rem !important;
    }
    tbody td {
        text-align: left;
        vertical-align: top;
        height: 5rem !important;
        width: calc(100% / 7) !important;
    }
}

.calendar-head {
    background: linear-gradient(to bottom right, #25c, #28f);
    color: #fff;
    position: relative;
    height: 3rem;
    line-height: 3rem;
}

.arrow-button {
    @include button;

    background: transparent;
    position: absolute;
    top: .5rem;
    color: #fff;
    width: 2rem;
    height: 2rem;
    border-radius: $border-radius;
    transition: 150ms ease;

    &:hover {
        background: rgba(#000, 0.1);
    }

    &:active {
        background: rgba(#000, 0.2);
        box-shadow: inset 0 2px 10px rgba(#000,0.25);
    }

    &:first-of-type {
        left: .5rem;
    }

    &:nth-of-type(2) {
        left: 3rem;
    }

    &:nth-of-type(3) {
        right: 3rem;
    }

    &:last-of-type {
        right: .5rem;
    }
}

// Calendar
.calendar-table {

    $cell-size: 3rem;

    border-collapse: collapse;
    border: none;
    margin: 0 auto;
    background: #fff;

    thead {
        border-bottom: 2px solid $line-colour;
    }

    td {
        padding: .5em;
        cursor: pointer;
        width: $cell-size;
        height: $cell-size;
        position: relative;
        &:not(:last-child) {
            border-right: 1px solid $line-colour;
        }
    }

    tbody td {
        transition: 150ms ease;
        &:hover {
            background: #eee;
        }
    }
    tbody tr:not(:last-child) {
        border-bottom: 1px solid $line-colour;
    }
}

.calendar-dots {
    @include ul;
    height: 4px;
    overflow: hidden;
    position: absolute;
    bottom: 4px;
    left: 4px;
    right: 4px;

    li {
        height: 4px;
        width: 4px;
        display: block;
        float: left;
        padding: 0;
        margin: 0;
        background: #28f;
        margin-right: 2px;
    }
}

.calendar-list {
    @include ul;
    position: absolute;
    bottom: 4px;
    left: 4px;
    right: 4px;

    li {
        display: block;
        padding: 2px;
        white-space: nowrap;
        overflow: hidden;
        font-size: .5rem;
        margin: 0;
        color: #fff;
        font-weight: 500;
        background: #28f;
        margin-top: 2px;
    }
}

.calendar-weekdays {
    background: #fff;
    color: #28f;
    font-weight: 300;
}

.calendar-body tr.calendar-weekdays {
    border-bottom: 2px solid $line-colour;
}

.calendar-body td.today {
    color: #28f;
    font-weight: 600;
}